}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  border: 0;
  border-radius: 22px;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--red {
  color: #fff;
  background-color: #d41342;
}

.btn--red:hover, .btn--red:focus {
  background-color: #c7133e;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.btn--light {
  color: #d41443;
  background-color: #fff;
}

.btn--light:hover, .btn--light:focus {
  background-color: #ecebeb;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

